<template>
  <div id="app">

    <div class="sidebar" :class="{expanded:expandView, hovered:showExpandedMenu, shadow:showExpandedMenu}" @mouseleave="hideOpenMenu">
      <b-navbar :sticky="true" variant="white" class="shadow-sm">
        <b-navbar-brand is="router-link" :to="{ name:'home'}" href="#" class="text-dark">Название</b-navbar-brand>
        <b-navbar-nav class="ml-auto">
          <b-button variant="outline-dark">Search</b-button>
        </b-navbar-nav>
      </b-navbar>
      <NewsList></NewsList>
    </div>

    <transition name="slideRight" >
    <div class="article-area " :class="{expanded:expandView}" v-show="detailView" >

      <b-navbar :sticky="true" variant="white" class="shadow-sm">
        <b-button v-on:click="toggleDetailView" variant="outline-dark" class="my-2 my-sm-0 d-block d-lg-none">Назад</b-button>
        <b-navbar-nav class="ml-auto">
          <b-button v-on:click="toggleExpand" variant="outline-dark" class="my-2 my-sm-0 d-none d-lg-block">Развернуть</b-button>
        </b-navbar-nav>
      </b-navbar>
      <div class="menu-area align-items-center justify-content-center" @mouseover="showHiddenMenu"><span>M</span></div>
      <b-container>
        <b-row>
          <b-col cols="12" >
            <router-view/>
          </b-col>
        </b-row>
      </b-container>
      <transition name="fade">
        <div v-if="showExpandedMenu" class="overlay" @click="hideOpenMenu"></div>
      </transition>
    </div>
    </transition>
  </div>
</template>
<script>
  // @ is an alias to /src
  import NewsList from '@/components/NewsList.vue'
  //import $ from 'jquery'; // ... jquery
  require('vue2-animate/dist/vue2-animate.min.css');


  export default {
    components: {
      NewsList
    },
    data: function(){
      return {
        showExpandedMenu: false
      }
    },
    computed:{
      detailView: function () {
        return this.$store.state.detailViewIsVisible
      },
      expandView: function () {
        return this.$store.state.detailViewExpand
      }
    },
    methods:{
      /**
       *
       * @param str String "on|off"
       */
      toggleDetailView: function ( str ) {
          this.$store.commit("detailViewToggle", str);
      },
      toggleExpand: function () {
        this.$store.commit("detailViewExpand");
      },
      showHiddenMenu: function () {
        this.showExpandedMenu = true;
      },
      hideOpenMenu: function () {
        if (this.expandView && this.showExpandedMenu) {
          this.showExpandedMenu = false;
        }
      }
    },
    mounted() {

      console.log(process.env.BASE_URL);

    }
  }
</script>
<style lang="scss">
  $sidebar: 450px;

  @mixin transition($transition...) {
    -moz-transition:    $transition;
    -o-transition:      $transition;
    -webkit-transition: $transition;
    transition:         $transition;
  }
  @mixin transition-property($property...) {
    -moz-transition-property:    $property;
    -o-transition-property:      $property;
    -webkit-transition-property: $property;
    transition-property:         $property;
  }
  @mixin transition-duration($duration...) {
    -moz-transition-property:    $duration;
    -o-transition-property:      $duration;
    -webkit-transition-property: $duration;
    transition-property:         $duration;
  }
  @mixin transition-timing-function($timing...) {
    -moz-transition-timing-function:    $timing;
    -o-transition-timing-function:      $timing;
    -webkit-transition-timing-function: $timing;
    transition-timing-function:         $timing;
  }
  @mixin transition-delay($delay...) {
    -moz-transition-delay:    $delay;
    -o-transition-delay:      $delay;
    -webkit-transition-delay: $delay;
    transition-delay:         $delay;
  }


