  }
  @media (min-width: 993px){
    display: block!important;

    &.slideRight-enter-active {
      animation: none;
    }

    &.expanded{
      margin-left:0;

      .menu-area{
        display: -ms-flexbox!important;
        display: -webkit-box!important;
        display: flex!important;
        /*z-index: 10000;*/
