        left: 0;
        top: 0;
        bottom: 0;
        width: 45px;
        height: 100%;
        background-color: rgba(199, 200, 201, 0.11);
      }
      .overlay{
        background-color: rgba(26, 27, 28, 0.43);
        /*filter: blur(8px);*/
