import Vue from 'vue'
import Vuex from 'vuex'
import VueLocalStorage from 'vue-localstorage'
import json from './assets/data.json'
import newsData from './assets/news.json'
import sourceData from './assets/source_list_new'

Vue.use(Vuex);
Vue.use(VueLocalStorage);

export default new Vuex.Store({
  state: {
    articles: json,
    news:newsData,
    sections:{
      exclusive:{
        title: "Публикации"
      },
      reports:{
        title: "Репортажи"
      },
      analysis:{
        title: "Комментарии"
      },
      commentaries:{
        title: "Аналитика"
      },
      discussion:{
        title: "Обсуждения"
      },
      news:{
        title: "Новости"
      },

    },
    source:{
      path:"",
      list:sourceData
    },
    detailViewIsVisible:false,
    detailViewExpand:false,
    basePath: process.env.NODE_ENV === "production" ? "/w-p/" : "/"
  },
  mutations: {
    addArticle(state,data){
      //TODO проверять на наличие
      state.articles.unshift(data);
    },
    load(state){
      /*try {
        let data = JSON.parse(json);
        state.articles = data;
      }
      catch (e) {
        console.log(e);
      }*/
    },
    /**
     *
     * @param state
     * @param str String "on|off"
     */
    detailViewToggle(state, str){
      if (str.length>0) {
        state.detailViewIsVisible = str === "on"? true : false;
      } else {
        state.detailViewIsVisible = !state.detailViewIsVisible;
      }

    },
    detailViewExpand(state){
      state.detailViewExpand = !state.detailViewExpand;
    }

  },
  getters: {
    articlesInSection: (state, getters) => sect => {
      //Не кэшируется
      return state.articles.filter(a => { return a.section === sect;  });
    },
    articlesExclusive: (state, getters) => {
      //Кэшируется
      //console.log(state.articles);
      return  state.articles.filter(a => { return a.section === "exclusive";  });
    },
    articlesReports: (state, getters) => {
      //Кэшируется
      //console.log(state.articles);
      return state.articles.filter(a => { return a.section === "reports";  });
    },
    getArticleByID: (state, getters) => id => {
      return state.articles.filter(a => { return a.id === id})
    },
    getNewsByID: (state, getters) => id => {
      return state.news.filter(a => { return a.id === id})
    },
    getSourceByID: (state, getters) => id => {
      return state.source.list.filter(a => { return a.id === id})
    }
  },
  actions: {
    getDetail(id){
      return articles.filter((a) => {
        return a.id.indexOf(id) !== -1
      })
    },
    getArticleListBySection({ commit, state }, payload){
      return state.articles.filter((a) => {
        //Точное
        return a.section === payload.section;
        //Поиск совпадения
        //return a.section.indexOf(payload.section) !== -1
      });


    },
    loadData () {

    }
  }
})
