<template>
  <b-list-group>
    <b-list-group-item is="router-link" v-for="item in itemGroup"
                       :key="item.id"
                       class="list-group-item flex-column align-items-start list-group-item-action"
                       @click.native="routerClick"
                         :to="{ name: 'detail', params: { sectionCode: item.section, id: item.id}}">

	    <div class="d-flex">

		    <div class="pt-4 pr-2">
			    <SourceImage v-if="getSource(item)" :itemSource="getSource(item)" :base="basePath" ></SourceImage>
		    </div>

		    <div class="flex-grow-1">
			    <small>{{getSource(item).title}}</small>
			    <h5 class="mb-1">{{item.title}}</h5>

			    <p v-if="desk" class="mb-1">{{item.desc}}</p>
			    <small>{{ atMoment(item.date)}}</small>
		    </div>
	    </div>
    </b-list-group-item>
  </b-list-group>
</template>
<script>

  import moment from 'moment';
  moment.locale("ru");

  import SourceImage from '@/components/SourceImage.vue'

export default {
  name: 'list-item',
  props: ['itemGroup', 'showDesc'],
  components: {
    SourceImage
  },
  data: function () {
    return{
      desk: this.showDesc && this.item.desc,
	  basePath: this.$store.state.basePath
    }
  },
  methods:{
    atMoment: function (date) {
      return moment(date, "DD.MM.YYYY HH:mm").fromNow()
    },
    routerClick: function () {
      this.$store.commit("detailViewToggle", "on");
    },
    getSource: function (item) {
      let val= item.source;
      if (item.source){
        let tmpVal = this.$store.getters.getSourceByID(item.source.id);
        if (tmpVal.length > 0)
          val = tmpVal[0];
      }
      return val;
    }

  }
}
</script>

